
        const data = [{"title":"Многопользовательская сборка ракетного двигателя","description":"\nПриложение дает пользователям возможность осуществлять процесс сборки предоставленной модели в формате виртуальной реальности. Люди могут собирать ракетный двигатель как самостоятельно, так и в многопользовательской комнате, объединившись с напарниками.\nПроцесс сопровождают различные подсказки и тексты с описаниями деталей. Все это, вкупе с глубоким погружением, способствует быстрому усвоению материала. При окончании сборки модель становится анимированной и демонстрирует свой принцип работы.","images":["https://user-images.githubusercontent.com/46666053/141160470-a31d20dd-86bd-4be8-9308-a631227b5ba0.png","https://user-images.githubusercontent.com/46666053/141155619-bb28a0e8-fd3b-4359-97e5-36f3d1cf71c3.png","https://user-images.githubusercontent.com/46666053/141155697-f247130d-e9a9-4f7d-812c-7a328e3022af.png","https://user-images.githubusercontent.com/46666053/141155722-2e5c8eab-dfef-42f7-ae67-daf43e1756d6.png"],"videos":["https://www.youtube.com/embed/6FTMDRp5fN4"],"tags":["VR","Game"],"tech":["Unity","C#"],"developers":["Сиротенко М.Р.","Корчиков М.Д.","Смирнов М.А","Лысаков И.А.","Смирнов Я.А."],"site":null,"sourceCode":[{"name":"Исходный код","link":"https://github.com/RTUITLab/Engine-VR"}],"date":"02/03/2022"},{"title":"ARtillery","description":"Игровое приложение в дополненной реальности для Android (ранее Android, iOS).\nЗадача игрока – продержаться как можно дольше, отстреливаясь от противников, появляющихся в пространстве вокруг него. Для защиты игрок может перемещать и наклонять телефон, а также сбивать летящие в него снаряды. Для стрельбы нужно коснуться экрана. Со временем сложность увеличивается.\nСуществует три типа противников с разными моделями поведения и сложностью, каждый из которых появляется реже или чаще других.\nПриложение реализовано с использованием ARCore, что позволяет ему работать без привязки к печатной метке.\nРанняя версия приложения была реализована с использованием Vuforia, для работы требуется распечатать метку.","images":["https://github.com/RTUITLab/ARtillery/raw/master/ReadmeMedia/ARtillery_Moment3.jpg","https://github.com/RTUITLab/ARtillery/raw/master/ReadmeMedia/ARtillery_Moment-min.jpg","https://github.com/RTUITLab/ARtillery/raw/master/ReadmeMedia/ARtillery_Moment2-min.jpg","https://github.com/RTUITLab/ARtillery/raw/master/ReadmeMedia/ARtilleryAction.gif"],"videos":["https://www.youtube.com/embed/BIoWlE25bD4"],"tags":["AR","Unity"],"tech":["Unity"],"developers":["Поздняков Д.В."],"sourceCode":[{"name":"Проект","link":"https://github.com/RTUITLab/ARtillery"}],"date":"02/03/2022"},{"title":"GyroBall","description":"Мобильное приложение – игра для проверки удобства использования механики акселерометра.\nИспользование поворотов телефона для управления шариком. У шарика предполагается 4 состояния, соответствующих стихиям, каждое состояние несёт в себе определённую соответствующую механику. Локации к игре будут соответствовать стихийной тематике.","images":["https://github.com/RTUITLab/GyroBall/raw/main/landing/23.JPG","https://github.com/RTUITLab/GyroBall/raw/main/landing/21.JPG","https://github.com/RTUITLab/GyroBall/raw/main/landing/1.png","https://github.com/RTUITLab/GyroBall/raw/main/landing/2.png","https://github.com/RTUITLab/GyroBall/raw/main/landing/3.png"],"videos":[],"tags":["Mobile"],"tech":["C#","3D modeling","Unity 3D"],"developers":["Баканова М.В.","Новаков Н.Ю."],"site":null,"sourceCode":[{"name":"Проект","link":"https://github.com/RTUITLab/GyroBall"}],"date":"02/03/2022"},{"title":"GeoHelper","description":"\nПроект для реализации AR-слоя мира, в котором отображаются объекты дополненной реальности в зависимости от заданного географического положения.\nПроект состоит из сайта и приложений для платформ Android и IOS.\nСайт предназначен для менеджмента данными, представляемых в AR-слое.\nМобильное приложение служит для отображения AR-слоя мира на экране используемого устройства.","images":["https://github.com/RTUITLab/GeoHelperUnity/raw/master/landing/1.png","https://github.com/RTUITLab/GeoHelperUnity/raw/master/landing/2.png","https://github.com/RTUITLab/GeoHelperUnity/raw/master/landing/3.png","https://github.com/RTUITLab/GeoHelperUnity/raw/master/landing/4.png"],"videos":[],"tags":["Frontend","Backend","Mobile","AR"],"tech":["Vue","Node.js","Unity","AR Foundation","C#"],"developers":["Лаптев И.А.","Китанин С.С.","Быковская О.C.","Смирнов Я.А."],"site":null,"sourceCode":[{"name":"Фронтенд","link":"https://github.com/RTUITLab/GeoHelper-Front"},{"name":"Бэкенд","link":"https://github.com/RTUITLab/GeoHelper-Back"},{"name":"Unity проект (приложение для Android или Ios)","link":"https://github.com/RTUITLab/GeoHelperUnity"}],"date":"02/03/2022"},{"title":"MicroFileServer (файловый сервер для небольших файлов)","description":"Данный сервис был разработан для добавления к отчетам сотрудников лаборатории\nдополнительных файлов (референсных картинок, видеодемонстраций и т.д.)","images":["https://github.com/RTUITLab/ITLab-MFS/raw/master/"],"videos":[],"tags":["Backend"],"tech":["Go","MongoDB"],"developers":["Комар Б.Г."],"site":null,"sourceCode":[],"date":"02/03/2022"},{"title":"ЗИЛ","description":"Проект для futurift кресла, который помещает игрока в зону боевых действий, где необходимо оторваться от преследования.","images":["https://github.com/RTUITLab/Project-3IL/raw/master/landing/0.png","https://github.com/RTUITLab/Project-3IL/raw/master/landing/1.png","https://github.com/RTUITLab/Project-3IL/raw/master/landing/2.png","https://github.com/RTUITLab/Project-3IL/raw/master/landing/3.png"],"videos":[],"tags":["VR"],"tech":["Unity","Steam VR","C#"],"developers":["Смирнов М.А.","Новаков Н.Ю.","Егоркин Н.Е.","Валяев Д.А.","Макаров Д.В.","Макущенко М.А.","Баканова М.В.","Шорин И.А.","Иерусалимов И.Д."],"site":null,"sourceCode":[{"name":"Source code","link":"https://github.com/RTUITLab/Project-3IL"}],"date":"02/03/2022"},{"title":"Plane crash simulator","description":"Симулятор, помещающий пользователя в ситуацию, когда он находится в падающем самолете","images":["https://github.com/RTUITLab/PlaneCrashSimulator/raw/master/landing/1.png","https://github.com/RTUITLab/PlaneCrashSimulator/raw/master/landing/2.png","https://github.com/RTUITLab/PlaneCrashSimulator/raw/master/landing/3.png"],"videos":[],"tags":["VR"],"tech":["Unity","Steam VR","C#"],"developers":["Валяев Д.А.","Шорин И.А."],"site":null,"sourceCode":[{"name":"Source code","link":"https://github.com/RTUITLab/PlainCrashSimulator"}],"date":"02/03/2022"},{"title":"ITLab (система управления лабораторией)","description":"Для организации работы людей в лаборатории создана система, учитывающая активность людей, участие в мероприятиях, оплату работы сотрудников. Система позволяет распределять нагрузку, обеспечивать прозрачность выполняемых действий и мероприятий. Сайт создан для внутреннего использования и доступ к имеют только сотрудники лаборатории.\nПроект состоит из нескольких бекендов, написанный на разных языках программирования, таких как C#, GO, Kotlin. Фронтенд реализован на фреймворке Vue.JS. Мобильное приложение для iOS реализовано на языке Swift с использованием фреймворка SwiftUI.\nТак же система имеет возможность уведомлять людей о изменениях, при помощи бота вконтакте или почты.","images":["https://github.com/RTUITLab/ITLab/raw/master/landing/1.png","https://github.com/RTUITLab/ITLab/raw/master/landing/2.png","https://github.com/RTUITLab/ITLab/raw/master/landing/ios_1.png","https://github.com/RTUITLab/ITLab/raw/master/landing/ios_2.png"],"videos":[],"tags":["Frontend","Backend","Mobile"],"tech":["Vue","ASP","Ktor","Swift"],"developers":["Макущенко М.А.","Романов Д.Е.","Комар Б.Г.","Кузнецов А.А.","Иванов М.Е."],"site":null,"sourceCode":[{"name":"Фронтенд","link":"https://github.com/RTUITLab/ITLab-Front"},{"name":"Система событий/оборудования","link":"https://github.com/RTUITLab/ITLab-Back"},{"name":"Система уведомлений","link":"https://github.com/RTUITLab/ITLab-Notify"},{"name":"Система записей о работе","link":"https://github.com/RTUITLab/ITLab-Reports"},{"name":"Система учета зарплат","link":"https://github.com/RTUITLab/ITLab-Salary"},{"name":"Приложение для iOS","link":"https://github.com/RTUITLab/ITLab-iOS"}],"date":"02/03/2022"},{"title":"Шутер в виртуальной реальности","description":"\nШутер в формате виртуальной реальности, позволяющий оттачивать стрельбу из оружия разных видов и уворачиваться от вражеских пуль.\n\nВ игре можно бросить вызов сразу нескольким противникам, либо сначала потренироваться в спокойной обстановке. В таком режиме у пользователя есть возможность стрелять по бутылкам в баре и мишеням в тире.\n\nНа выбор представлено 5 моделей оружия, 4 локации и 3 уровня сложности.","images":["https://user-images.githubusercontent.com/57914366/148579235-a4f9e25e-dc3c-49d8-b1a6-7dbabfa52a99.jpg","https://user-images.githubusercontent.com/57914366/148580147-297f79e2-15e3-4b34-8108-9550982b0fe2.jpg","https://user-images.githubusercontent.com/57914366/148580160-84ee7b40-83f9-439c-a37e-4e1184bd4dd5.jpg","https://user-images.githubusercontent.com/57914366/148580059-71f366be-9f6c-44bf-a662-abf7e3e00003.jpg"],"videos":[],"tags":["VR","Game"],"tech":["Unity","C#"],"developers":["Егоркин Н.Е.","Шорин И.А."],"site":null,"sourceCode":[{"name":"Исходный код","link":"https://github.com/RTUITLab/VRDuel"}],"date":"02/03/2022"},{"title":"Виртуальный ситуационный центр","description":"Виртуальный ситуационный центр позволяет множеству людей в любой точке планеты взаимодействовать между собой в единой среде виртуальной реальности. При этом возможно использование любого доступного на данный момент шлема виртуальной реальности. Также сервисом можно пользоваться без шлема, используя клавиатуру и мышь.\nВ сервисе реализована концепция комнат, к которым подключаются люди, и их общение происходит только внутри данного пространства. Разговоры и обмен файлами не выйдут за пределы выделенного пространства, что обеспечивает конфиденциальность приватного общения.\nВнутри выделенного виртуального пространства люди могут общаться при помощи голосового чата, могут обмениваться документами из облачных хранилищ (OneDrive, Google drive…), просматривать их, комментировать и обсуждать.\nВ данный момент сервис находится на стадии открытого тестирования.","images":["https://files.rtuitlab.dev/landing_src/situation_center/1.png","https://files.rtuitlab.dev/landing_src/situation_center/2.png"],"videos":["https://www.youtube.com/embed/HRFRQa3tMcI"],"tags":["VR","Social"],"tech":["NodeJS","ASP. net core","Unreal Engine"],"developers":["Макущенко М.А.","Иванов И.Е."],"site":null,"sourceCode":[],"date":"02/03/2022"},{"title":"BunnyHuntVR - Web VR шутер","description":"VR игра, обороняйте грядки от проворных и назойливых зайцев. Узнайте сколько морковки вы сможете сохранить.","images":["https://github.com/RTUITLab/WebVRShoot/raw/master/LANDING/BunnyHuntVR.png","https://github.com/RTUITLab/WebVRShoot/raw/master/LANDING/BunnyHuntVR2.png"],"videos":[],"tags":["VR","Game","Shooter"],"tech":["A-Frame","Javascript","Html"],"developers":["Соколов А.В","Иерусалимов И.Д."],"site":"https://webvrshoot.rtuitlab.dev/","sourceCode":[{"name":"Проект","link":"https://github.com/RTUITLab/WebVRShoot"}],"date":"25/01/2022"},{"title":"VRTherapy - Приложение для релаксации в VR со множеством разнообразных активностей","description":"В VRTherapy вы можете погрузиться в захватывающую и успокаивающую виртуальную реальность с приятным звуковым сопровождением.\n\nОтдохните у виртуального костра, прогуляйтесь по летнему лесу, послушайте шум прибоя или же сыграйте в мини-игру, где вам предстоит управлять бумажным самолётиком на ветру.\n\nДанная игра предназначена исключительно для запуска в портативном VR-шлеме для Android, например, Google Cardboard.","images":["https://github.com/RTUITLab/VRTherapy/raw/AfterJunction/LANDING/screenshot1.png","https://github.com/RTUITLab/VRTherapy/raw/AfterJunction/LANDING/screenshot2.png","https://github.com/RTUITLab/VRTherapy/raw/AfterJunction/LANDING/screenshot3.png","https://github.com/RTUITLab/VRTherapy/raw/AfterJunction/LANDING/screenshot4.png"],"videos":[],"tags":["VR","Game","Simulator","Arcade"],"tech":["Unity","Android","Google VR","Google Cardboard"],"developers":["Баканова М.","Левандровский А.","Матчин А.","Новаков Н."],"site":null,"sourceCode":[{"name":"Проект","link":"https://github.com/RTUITLab/VRTherapy"}],"date":"02/03/2022"}];
        export default data;
        